* {
  box-sizing: border-box;
}

.WelcomeScreen {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #ffcdbe;
  border: 1px solid red;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Quicksand", sans-serif;
}

.inner-wrapper {
  text-align: center;
  width: 100%;
  max-width: 920px;
  border: 1px solid #f2f2f2;
  border-radius: 4px 12px 12px 12px;
  background-color: white;
  margin-bottom: 16px;
  box-shadow: 0px 2px 0px 2px rgba(0, 0, 0, 0.04);
  padding: 20px;
  color: #18171c;
}

h1 {
  font-family: "Pacifico", cursive;
  color: #ff5e2c;
  text-align: center;
  margin-bottom: 40px;
  font-size: 40px;
}

h1 span {
  color: #009eec;
  text-shadow: none;
}

.button_cont {
  margin: 40px 0;
}

.google-btn {
  border-radius: 1px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
  transition: background-color 0.218s, border-color 0.218s, box-shadow 0.218s;
  background-color: #fff;
  background-image: none;
  color: #262626;
  cursor: pointer;
  outline: none;
  overflow: hidden;
  border: 1px solid #4285f4;

  width: 240px;
  height: 50px;
  display: flex;
  flex-direction: row;
}

.google-btn:hover {
  box-shadow: 0 0 3px 3px rgba(66, 133, 244, 0.3);
}

.google-btn:active {
  background: #1669f2;
}

.google-icon-wrapper {
  width: 50px;
  height: 50px;
  border-radius: 2px;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.google-icon {
  width: 18px;
  height: 18px;
}

.btn-text {
  cursor: pointer;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 48px;
  font-weight: 500;
  letter-spacing: 0.21px;

  color: #fff;
  border: none;

  background-color: #4285f4;
  width: 80%;
  height: 100%;
  text-align: center;
}

a {
  color: #009eec;
}
